import React from "react";
import { Profile, MenuList } from "components";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import LocalShippingIcon from "@material-ui/icons/LocalShipping";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  icon: {
    width: "20px",
    height: "20px",
    color: "#ffffff",
  },
  keranjang: {
    backgroundColor: "#1de9b6",
    width: "30px",
    height: "30px",
  },
  pengiriman: {
    backgroundColor: "#03a9f4",
    width: "30px",
    height: "30px",
  },
  exit: {
    backgroundColor: "#ffc107",
    width: "30px",
    height: "30px",
  },
});

function Akun() {
  const classes = useStyles();
  const dataMenu = [
    {
      icon: <ShoppingCartIcon className={classes.icon} />,
      href: "/produk-view",
      title: "Keranjang Belanja",
      class: classes.keranjang,
    },
    {
      icon: <LocalShippingIcon className={classes.icon} />,
      href: "/produk-view",
      title: "Pengiriman",
      class: classes.pengiriman,
    },
  ];
  const dataLogout = [
    {
      icon: <ExitToAppIcon className={classes.icon} />,
      href: "/produk-view",
      title: "Sign-out",
      class: classes.exit,
    },
  ];
  return (
    <React.Fragment>
      <Profile />
      <Box marginTop="15px">
        <MenuList data={dataMenu} />
      </Box>
      <Box marginTop="15px">
        <MenuList data={dataLogout} />
      </Box>
    </React.Fragment>
  );
}

export default Akun;
