import React from "react";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import { Avatar, Box, Divider, IconButton } from "@material-ui/core";
import { Link } from "react-router-dom";

function MenuList(props) {
  const { data } = props;
  return (
    <React.Fragment>
      {data.map((value, index) => (
        <Link key={index} to={value.href} style={{ textDecoration: "none" }}>
          <Box
            display="flex"
            alignItems="center"
            height="50px"
            bgcolor="#ffffff"
            padding="0 12px"
          >
            <Avatar sizes="30px" className={value.class}>
              {value.icon}
            </Avatar>
            <Box paddingLeft="5px" flexGrow={1}>
              {value.title}
            </Box>
            <IconButton aria-label="tab">
              <ChevronRightIcon />
            </IconButton>
          </Box>
          <Divider />
        </Link>
      ))}
    </React.Fragment>
  );
}

export default MenuList;
